body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'DM Sans', sans-serif;
}
:root{
  --base-color: #171717;
  --text-color: #ffffff;
  --positive-color: #27AE60;
  --font-card-primary: 'Baloo Da 2', serif ;
  --font-card-secundary: 'Josefin Slab', serif;
  --font-act: 'Muli', serif;
}
.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  background-color: #171717;
  background-color: var(--base-color);

}


nav.navbar{
  width: 80%;
  padding: .7rem;
  display: flex;
  justify-content: space-between;
  font-family: 'DM Sans';
  transition: 1s; 
}
div.logo{
  text-align: center;
  width: 40%;
}
.logo h1{
  color: var(--text-color);
  font-weight: 700;
  font-style: normal;
  font-size: 2.3rem;
  line-height: 47px;
}
.logo span{
  color: var(--positive-color);
}
ul{
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
li{
  list-style: none;
}
a.nav-link{
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 23px;
  color: var(--text-color);
  transition: 0.8s;
  text-decoration: none;
}
a.nav-link:hover{
  color: var(--positive-color);
}

