    border-radius: 50%;
  }
  .box::before{
    content: "";
    position: absolute;
    inset: -10px 140px;
    transition: 0.5s;
    background: linear-gradient(315deg, #00ccff, #d400d4);
    -webkit-animation: loopBorder 4s linear infinite;
            animation: loopBorder 4s linear infinite;
  }
  .box:hover::before{
    inset: -20px 0px;
  }
  .box::after{
    content: "";
    position: absolute;
    inset: 6px;
    background-color: var(--base-color);
    border-radius: 50%;
    z-index: 1;
  }
  .alan-img{
    position: absolute;
    inset: 30px;
