.cards{
  width: 18rem;
  height: 25rem;
  position: relative;
}

.card-img-wrapper{
  width: 95%;
  height: 100%;
  background-color: rgba(0,0,0,.8);  
  border-radius: .5rem;
}

.card-img-wrapper img{
  object-fit: cover;
  width: 100%;
  height: 100%;  
  opacity: .8;
  border-radius: .5rem;
  transition: all .3s;
}

.cards:hover .card-img-wrapper img{
  opacity: .5;
}

.card-info{
  position: absolute;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  padding: 2rem;
  text-shadow: 0.3rem 0.2rem 0.3rem rgba(0, 0, 0, .6);
  transition: all .5s;
}

.card-info h2{
  font-family: var(--font-card-primary);
  font-size: 2rem;
  font-weight: 500;
  color: #eee;
  margin-bottom: .5rem;
}

.card-info h3{
  font-family: var(--font-card-secundary);
  font-weight: bolder;
  color: var(--positive-color);
  margin-bottom: .5rem;
}

.card-info p{
  font-family: var(--font-card-secundary);
  color: #eee;
  font-weight: bolder;
  width: 80%;
  margin-bottom: .5rem;
}

.card-info a{
  width: 8rem;
  height: 2.5rem;
  background: #3a6186;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #00ccff, #d400d4);
  font-family: var(--font-act);

  color: #eee;
  border-radius: .5rem;
  border: none;
  box-shadow: 0.1rem .3rem  rgba(0, 0, 0, .3);
}

.cards:hover .card-info{
  opacity: 1;
  bottom: 2rem;
  visibility: visible;
}

