  font-weight: 500;
  color: #eee;
  margin-bottom: .5rem;
}

.card-info h3{
  font-family: var(--font-card-secundary);
  font-weight: bolder;
  color: var(--positive-color);
  margin-bottom: .5rem;
}

.card-info p{
  font-family: var(--font-card-secundary);
  color: #eee;
  font-weight: bolder;
  width: 80%;
  margin-bottom: .5rem;
}

.card-info a{
  width: 8rem;
  height: 2.5rem;
  background: #3a6186;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #00ccff, #d400d4);
  font-family: var(--font-act);

  color: #eee;
  border-radius: .5rem;
  border: none;
  box-shadow: 0.1rem .3rem  rgba(0, 0, 0, .3);
}

.cards:hover .card-info{
  opacity: 1;
  bottom: 2rem;
  visibility: visible;
}


